import React from 'react'
import { Col, Input, Layout, Menu, Row, Button, Progress } from 'antd'
import HeaderHorizontal from '../../../../layout/components/header/HeaderHorizontal'
import MenuFooter from '../../../../layout/components/menu/footer'
import CustomiseTheme from '../../../../layout/components/customise'
import ScrollTop from '../../../../layout/components/scroll-to-top'
import { useSelector } from 'react-redux'
import { Box, IconButton, MenuItem, Tab, Tabs, TextField, Typography, } from '@mui/material'
const { TextArea } = Input;
import ThumbDownOffAltIcon from '@mui/icons-material/ThumbDownOffAlt';
import ThumbUpOffAltIcon from '@mui/icons-material/ThumbUpOffAlt';
import { FormattedMessage } from 'react-intl'


const RightPane = (props) => {

    const { aiRecommendation, generateSemantic, translate, workSpaceId, summary, markAsFav, workSpaceName, setModalFavOpen } = props;
    return (
        <Box component={'section'} sx={{ display: 'flex', flexDirection: 'column', rowGap: '20px' }}>

            <Box sx={{ display: 'flex', flexDirection: 'column' }}>
                <Box mb={1.5} sx={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center' }}>
                    <Typography fontWeight={600} fontSize={15}><FormattedMessage id="workspace-ai" /></Typography>
                    <Box sx={{ display: 'flex', columnGap: '7px' }}>
                        <Button disabled={!generateSemantic || !aiRecommendation || aiRecommendation === ''} onClick={() => setModalFavOpen(true)}><FormattedMessage id="workspace-favourite" /></Button>
                        <Button><FormattedMessage id="workspace-export" /></Button>
                    </Box>
                </Box>

                {/* <TextArea rows={9} value={text} /> */}
                <Box p={2} sx={{ height: '260px', backgroundColor: '#eef2ff', borderRadius: '7px', border: '1px solid #b9bcf8', boxSizing: 'border-box', overflowY: 'auto' }}>
                    {aiRecommendation}
                </Box>
            </Box>

            <Box mt={1} sx={{ display: 'flex', flexDirection: 'column', rowGap: '15px' }}>
                <Box sx={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center' }}>
                    <Typography fontWeight={600} fontSize={15}><FormattedMessage id="workspace-semantic-search" /></Typography>
                </Box>


                {
                    generateSemantic?.map(result => (
                        <Box key={result.verdictId} sx={{ display: 'flex', flexDirection: 'column', borderRadius: '9px', backgroundColor: 'white', height: 'fit-content' }}>
                            <Box pl={3} pr={3} pt={2} sx={{ display: 'flex', flexDirection: 'column', alignItems: 'space-between', boxSizing: 'border-box' }}>
                                <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
                                    <Typography fontWeight={600} fontSize={15}>{result.verdictId} {' on '} {result.verdictDate}</Typography>              
                                    <Box sx={{ width: '40%' }}> 
                                        <Progress percent={result.similarityScore.toFixed(2)} size='large' strokeColor={'green'} />

                                    </Box>
                                </Box>
                                <Typography sx={{ mt: 2, color: '#8c939b' }} fontSize={14}>
                                    {result.pageContent}
                                </Typography>
                                <Typography component={'span'} sx={{ mt: 2 }} fontWeight={600} fontSize={14}>URL: <Typography sx={{ color: '#8c939b' }} fontSize={14} component={'a'}>{result.url}</Typography></Typography>

                            </Box>

                            <Box mt={3} sx={{ borderTop: '0.5px solid silver' }}>
                                <Box pl={2} pr={7} pt={1.5} pb={1} sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', boxSizing: 'border-box' }}>
                                    <Box sx={{ display: 'flex', }}>
                                        <IconButton>
                                            <ThumbUpOffAltIcon />
                                        </IconButton>
                                        <IconButton>
                                            <ThumbDownOffAltIcon />
                                        </IconButton>
                                    </Box>

                                    <Box sx={{ display: 'flex', columnGap: '25px' }}>
                                        <TextField
                                            select
                                            label=<FormattedMessage id="workspace-2credits" />
                                            defaultValue="Translate into"
                                            // SelectProps={{
                                            //     native: true,
                                            // }}
                                            sx={{ width: '150px', }}

                                        >
                                            <MenuItem defaultValue={'Translate into'} value={'Translate into'}>
                                                <FormattedMessage id="workspace-translate" />
                                            </MenuItem>
                                            <MenuItem onClick={(e) => translate({language:e.target.innerText, workSpaceId:workSpaceId, article:result?.pageContent, totalCreditsUsed:2})} value={'German'}>
                                            <FormattedMessage id="workspace-german" />
                                            </MenuItem>
                                            <MenuItem onClick={(e) => translate({language:e.target.innerText, workSpaceId:workSpaceId, article:result?.pageContent, totalCreditsUsed:2})} value={'German'}>
                                            <FormattedMessage id="workspace-english" />
                                            </MenuItem>
                                            <MenuItem onClick={(e) => translate({language:e.target.innerText, workSpaceId:workSpaceId, article:result?.pageContent, totalCreditsUsed:2})} value={'Arabic'}>
                                            <FormattedMessage id="workspace-urdu" />
                                            </MenuItem>
                                        </TextField>

                                        <Button onClick={() => summary({ article:result?.pageContent,  workSpaceId:workSpaceId, totalCreditsUsed:2 })} ><FormattedMessage id="workspace-summary" /></Button>
                                    </Box>
                                </Box>
                            </Box>

                        </Box>
                    ))
                }



            </Box>

        </Box>
    )
}

export default RightPane