import React, { useState } from 'react'
import { Col, Input, Layout, Menu, Row, Button, Modal, message } from 'antd'
import HeaderHorizontal from '../../../../layout/components/header/HeaderHorizontal'
import MenuFooter from '../../../../layout/components/menu/footer'
import CustomiseTheme from '../../../../layout/components/customise'
import ScrollTop from '../../../../layout/components/scroll-to-top'
import { useDispatch, useSelector } from 'react-redux'
import { Box, CircularProgress, IconButton, MenuItem, Tab, Tabs, TextField, Typography, } from '@mui/material'
import LeftPane from './LeftPane'
const { TextArea } = Input;
import ThumbDownOffAltIcon from '@mui/icons-material/ThumbDownOffAlt';
import ThumbUpOffAltIcon from '@mui/icons-material/ThumbUpOffAlt';
import RightPane from './rightPane'
import ProtectedAppPage from "../../../pages/Protected"
import { getSummary, getTranslation, getWorkpsace, markFav } from '../../../../redux/workspace/workspaceActions'
import { useEffect } from 'react'




const Workspace = () => {
    const dispatch = useDispatch();
    const customise = useSelector(state => state.customise)

    //Modal state (Translation)
    const [modal2Open, setModal2Open] = useState(false);
    const [translatedContent, setTranslatedContent] = useState('')
    const [fetchingTranslation, setfetchingTranslation] = useState(false)

    // Modal State (Summary) 
    const [modalSummaryOpen, setModalSummaryOpen] = useState(false);
    const [summarizedContent, setSummarizedContent] = useState('')
    const [fetchingSummary, setFetchingSummary] = useState(false)

    // Modal State (Favourite) 
    const [modalFavOpen, setModalFavOpen] = useState(false);
    const [workSpaceName, setWorkSpaceName] = useState('');

    //left pane state
    const [content, setContent] = useState('')
    const [specificQuestion, setSpecificQuestion] = useState('')
    const [noOfResults, setNoOfResults] = useState(2);
    const [value, setValue] = useState(0);
    const [fetchingResults, setFetchingResults] = useState(false);


    //right pane state
    const [aiRecommendation, setAiRecommendation] = useState('')
    const [generateSemantic, setGenerateSemantic] = useState()
    const [workSpaceId, setWorkSpaceId] = useState('')

    const handleChange = (e, v) => {
        setValue(v);
    }

    const returnInitialState = () => {
        setContent('');
        setSpecificQuestion('');
        setNoOfResults(2);
        setValue(0);
        setAiRecommendation('');
        setGenerateSemantic();
        setWorkSpaceId('');
        setWorkSpaceName('');
        setFetchingResults(false);
        setFetchingSummary(false);
        setfetchingTranslation(false);
        setSummarizedContent('');
        setTranslatedContent('');


    }



    // API calls
    const generateResults = async (param) => {
        if (content.trim() === '') {
            message.error("Please fill content field")
            return
        }
        let finalParam;
        if (specificQuestion.trim() !==''){
            finalParam = {...param, question:specificQuestion};
        } else {
            finalParam = param;
        }

        setFetchingResults(true)
        const res = await dispatch(getWorkpsace(finalParam));

        setAiRecommendation(res?.aiRecommendation);
        setGenerateSemantic(res?.results);
        setWorkSpaceId(res?.id);
        setFetchingResults(false)
    }

    const translate = async (param) => {
        setfetchingTranslation(true)
        setModal2Open(true)
        console.log(param, 'translation')
        const res = await dispatch(getTranslation(param))
        setTranslatedContent(res?.data);
        setfetchingTranslation(false)
        console.log(res, 'res of translation')
    }

    const summary = async(param) => {
        console.log(param,'param')
        setFetchingSummary(true)
        setModalSummaryOpen(true)
        const res = await dispatch(getSummary(param))
        setSummarizedContent(res?.data);
        setFetchingSummary(false)
        console.log(res)
    }

    const markAsFav = async (param) => {
        console.log(param,'param')
        const res = await dispatch(markFav(param))
        setModalFavOpen(false);
        setWorkSpaceName('');
        message.success(res?.message)
    }



    useEffect(() => {
        console.log(workSpaceId, ' sematic')
    }, [workSpaceId])


    const leftPaneProps = {
        specificQuestion,
        setSpecificQuestion,
        content,
        setContent,
        value,
        handleChange,
        noOfResults,
        setNoOfResults,
        generateResults,
        returnInitialState,
        fetchingResults
    }

    const rightPaneProps = {
        generateSemantic,
        aiRecommendation,
        translate,
        workSpaceId,
        summary,
        markAsFav,
        workSpaceName,
        setModalFavOpen

    }
    return (
        <Layout className={`hp-app-layout hp-bg-color-black-20 hp-bg-color-dark-90 ${customise.navigationBg && 'hp-app-layout-bg'}`}>
            {/* <HeaderHorizontal visible={visible} setVisible={setVisible} /> */}


            {/* <Row >
                <Col span={8} >
                    <div style={{backgroundColor:'red'}}>dwawd</div>
                </Col>
                <Col span={15}>
                <div style={{backgroundColor:'green'}}>dwawd</div>
                </Col>
            </Row> */}

            <Box sx={{ display: 'flex', backgroundColor: 'white', '@media(max-width:1200px)': { flexDirection: 'column' } }}>
                <Box sx={{ width: '35%', backgroundColor: 'white', flexDirection: 'column', padding: '20px 20px', borderRight: '1px solid #f3f4f6', zIndex: 5, position: 'sticky', overflow: 'hidden', top: '0', height: 'fit-content', '@media(max-width:1200px)': { width: '100%', position: 'static', padding: '30px 30px' } }}>
                    <LeftPane {...leftPaneProps} />


                </Box>
                <Box pl={4} pr={4} pt={5} pb={4} sx={{ width: '64%', backgroundColor: '#f3f4f6', boxSizing: 'border-box', overflowY: 'auto', '@media(max-width:1200px)': { width: '100%', } }}>

                    <RightPane {...rightPaneProps} />

                </Box>


            </Box>
            {/* // Translation modal */}
            <Modal
                title= {!fetchingTranslation ?"Translated content" :"Translating content ..."}
                centered
                visible={modal2Open}
                onOk={() => setModal2Open(false)}
                onCancel={() =>  {
                    setModal2Open(false)
                    setTranslatedContent('')
                }}
            >
                {
                    fetchingTranslation
                    ?
                    <Box sx={{display:'flex', justifyContent:'center'}}>
                        <CircularProgress />
                    </Box>
                    :
                    <Box p={2} sx={{  backgroundColor: '#eef2ff', borderRadius: '7px', border: '1px solid #b9bcf8', boxSizing: 'border-box', overflowY: 'auto' }}>
                        {translatedContent}
                    </Box>
                }
            </Modal>

            {/* // Summary modal */}
            <Modal
                title= {!fetchingSummary ?"Summarized content" :"Summarizing content ..."}
                centered
                visible={modalSummaryOpen}
                onOk={() => setModalSummaryOpen(false)}
                onCancel={() =>  {
                    setModalSummaryOpen(false)
                    setSummarizedContent('')
                }}
            >
                {
                    fetchingSummary
                    ?
                    <Box sx={{display:'flex', justifyContent:'center'}}>
                        <CircularProgress />
                    </Box>
                    :
                    <Box p={2} sx={{  backgroundColor: '#eef2ff', borderRadius: '7px', border: '1px solid #b9bcf8', boxSizing: 'border-box', overflowY: 'auto' }}>
                        {summarizedContent}
                    </Box>
                }
            </Modal>

            {/* // Fav modal */}
            <Modal
                title= {"Mark As Favourite"}
                centered
                visible={modalFavOpen}
                onOk={() => {
                    if (workSpaceName.trim() === '') {
                        message.error('Please enter your workspace name')
                        return
                    }
                     markAsFav({workSpaceId, name:workSpaceName})
                }}
                onCancel={() =>  {
                    setModalFavOpen(false)
                    setWorkSpaceName('')
                }}
            >
                {
                    <Input placeholder='Enter workspace name' value={workSpaceName} onChange={e => setWorkSpaceName(e.target.value)} style={{ width: '100%' }} />   
                }
            </Modal>


            <CustomiseTheme />
            <ProtectedAppPage />
            <ScrollTop />
        </Layout>
    )
}

export default Workspace