import React, { useState } from 'react'
import { Col, Input, Layout, Menu, Row, Button, Select, Form } from 'antd'
import HeaderHorizontal from '../../../../layout/components/header/HeaderHorizontal'
import MenuFooter from '../../../../layout/components/menu/footer'
import CustomiseTheme from '../../../../layout/components/customise'
import ScrollTop from '../../../../layout/components/scroll-to-top'
import { useSelector } from 'react-redux'
import { Box, MenuItem, Tab, Tabs, TextField, Typography, } from '@mui/material'
import { FormattedMessage } from 'react-intl'
const { TextArea } = Input;

const LeftPane = (props) => {
    const { specificQuestion, setSpecificQuestion, content, setContent, value, handleChange, noOfResults, setNoOfResults, generateResults, returnInitialState, fetchingResults } = props;
    
    return (
        <>
            <Button type='primary' onClick={() => returnInitialState()} >
                + <FormattedMessage id="workspace-new-workspace" />
            </Button>

            <Box mt={2} sx={{ borderBottom: 1, borderColor: 'divider' }}>
                <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
                    <Tab label={<FormattedMessage id="workspace-semantic-search" />} />
                    <Tab label={<FormattedMessage id="workspace-lexbot" />} />
                </Tabs>
            </Box>

            <Box mt={2} sx={{ display: 'flex', flexDirection: 'column' }}>
                <Box mb={0.5} sx={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center' }}>
                    <Box sx={{ display: 'flex', columnGap: '30px', alignItems: 'center' }}>
                        <Typography fontWeight={600} fontSize={15}><FormattedMessage id="workspace-content" /></Typography>
                        <Typography fontSize={13}>+2 <FormattedMessage id="workspace-credits" /></Typography>
                    </Box>
                    <Typography fontSize={13}>{content?.length}/200</Typography>
                </Box>

                <TextArea required value={content}  rows={7} onChange={(e) => setContent(e.target.value)} maxLength={200} placeholder='Content' />
            </Box>

            <Box mt={2} sx={{ display: 'flex', flexDirection: 'column' }}>
                <Box mb={0.5} sx={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center' }}>
                    <Box sx={{ display: 'flex', columnGap: '30px', alignItems: 'center' }}>
                        <Typography fontWeight={600} fontSize={15}><FormattedMessage id="workspace-specific-question" /></Typography>
                        {/* <Typography fontWeight={600} fontSize={13}>(optional)</Typography> */}
                    </Box>
                    <Typography fontSize={13}>{specificQuestion.length}/100</Typography>
                </Box>
                <TextArea required rows={4} maxLength={100} value={specificQuestion} onChange={(e) => setSpecificQuestion(e.target.value)} placeholder='Question' />
            </Box>


            <Box mt={2} sx={{ display: 'flex', flexDirection: 'column' }}>
                <Box sx={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center' }}>
                    <Box sx={{ display: 'flex', columnGap: '30px', alignItems: 'center' }}>
                        <Typography fontWeight={600} fontSize={15}><FormattedMessage id="workspace-how-many" /></Typography>
                    </Box>

                </Box>
                <Box mt={0.5} sx={{ display: 'flex', columnGap: '10px', alignItems: 'center' }}>
                    {/* <Input placeholder='no of result' style={{ width: '50%' }} /> */}
                    <Select
                        defaultValue={noOfResults}
                        value={noOfResults}
                        style={{
                            width: '50%',
                        }}
                        onChange={(value) => setNoOfResults(value)}
                        options={[
                            {
                                value: 2,
                                label: 2,
                            },
                            {
                                value: 3,
                                label: 3,
                            },
                            {
                                value: 4,
                                label: 4,
                            },
                            {
                                value: 5,
                                label: 5,
                            },
                        ]}
                    />
                    <Typography fontSize={13}>+{noOfResults} <FormattedMessage id="workspace-credits" /></Typography>
                </Box>
            </Box>

            <Box mt={3} sx={{ display: 'flex', flexDirection: 'column' }}>
                <Typography fontWeight={600} fontSize={15} sx={{ width: '50%' }}><FormattedMessage id="workspace-input-sources" /></Typography>
                <Box mt={0.5} sx={{ display: 'flex', columnGap: '10px', alignItems: 'center' }}>
                    <Input placeholder='4P.290/ 2005' style={{ width: '50%' }} />
                    {/* <Typography fontSize={13}>+2 <FormattedMessage id="workspace-credits" /></Typography> */}
                </Box>
                <Box mt={1.5} sx={{ display: 'flex', columnGap: '10px', alignItems: 'center' }}>
                    <Input placeholder='4P.290/ 2005' style={{ width: '50%' }} />
                </Box>
                <Box mt={1.5} sx={{ display: 'flex', columnGap: '10px', alignItems: 'center' }}>
                    <Input placeholder='4P.290/ 2005' style={{ width: '50%' }} />
                </Box>
            </Box>

            <Box mt={7} mb={1} sx={{ display: 'flex', justifyContent: 'flex-end', }}>

                <Box sx={{ backgroundColor: '#D3D3D3', width: '100px', height: '35px', display: 'flex', justifyContent: 'center', alignItems: 'center', borderRadius: '6px' }}>
                    <Typography fontSize={12}>+{2 + noOfResults} <FormattedMessage id="workspace-credits" /></Typography>
                </Box>

            </Box>

            <Button onClick={() => generateResults({ totalResults: noOfResults, context: content, totalCreditsUsed: 2 + noOfResults })} loading={fetchingResults} disabled={content?.length <= 0} style={{ width: '100%', }} type='primary'><FormattedMessage id="workspace-generate" /></Button>

        </>
    )
}

export default LeftPane